.web-chat-hoop {
    $this: &;
    height: auto;

    .eureka-tile__content-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__hoop-content {
        .eureka-callout {
            padding: 0;
            &__title{
                font-size: var(--eureka-core-typography-headings-level5-font-size);
            }
        }

        #{$this}__leaveMsgbtn {
            width: 100%;
            margin-top: 16px;
        }
    }
}
