@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
.web-chat-window-container {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 375px;
    $this: &;
    &__tile {
    
        z-index: 100;
        position: fixed;
        bottom: 0;
        width: 100%;
        max-width: 375px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 40%);
        border-radius: var(--eureka-chat-window-border-radius);
        @include eureka-flex-column;
    
        #{$this}__close-btn.eureka-button,
        #{$this}__minimize-btn.eureka-button {
            border-radius: 100%;
            background-color: var(--eureka-dialog-action-color-bg);
            width: 24px;
            height: 24px;
            vertical-align: middle;
            color: var(--eureka-core-color-primary);
            border: none;
    
            .eureka-button__content-container {
                padding: 0;
            }
    
            .eureka-button__icon-container {
                padding: 0;
            }
        }
    }

    &__title-btn-container--minimize-btn-container {
        margin-right: 16px;
    }

    .eureka-tile__heading-container {
        height: 44px;
        border-bottom: none;
        @include eureka-flex-row;
        #{$this}__title {
            flex: 1 0 auto;
            line-height: 2.4rem;
            font-weight: 600;
        }
    }
}
