:root {
    --chat-font: 'Averta';

    --chat-bubble-max-width: 266px;

    --web-chat-option-border-color: #e5e5e5;
    --web-chat-option-radius: 8px;
    --web-chat-option-text-color: #333;
    --web-chat-option-meta-color: #666;
    --icon-button-chat-background-color: #fff;
    --icon-button-chat-unread-count-color: #fff;
    --icon-button-chat-unread-count-background-color: #e0114c;
    --web-chat-option-icon-chevron-right-color: #ccc;
}
